@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Nunito:wght@600&display=swap');

.NavbarItems {
    background: #36454F;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 20px;
    list-style: none;
    text-align: center;
    width: 80vw;
    justify-content: end;
}

.nav-links {
    display: flex;
    color: #ffffff;
    text-decoration: none;
    padding: 0.2rem 1rem;
    font-family: 'Nunito', sans-serif;
}

.nav-links:hover {
    background-color: #ffffff;
    color: #36454F;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.navbar-logo {
    color: #ffffff;
    justify-self: start;
    margin-left: 2vw;
    cursor: pointer;
    text-decoration: none;
    font-size: larger;
    font-family: 'Montserrat', sans-serif;
    transition: all .2s ease-in-out;
}

.navbar-logo:hover {
   transform: scale(1.2);
}

.navbar-profile {
    display: flex;
   transition: all .2s ease-in-out;
   height: 30px;
}

.navbar-profile:hover {
   transform: scale(1.3);
}

.dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    right: 0px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
 
  .dropdown-content a:hover {background-color: #ddd;}
 
 .dropdown:hover .dropdown-content {display: block;}
 
 .dropdown:hover .dropbtn {background-color: #1b5f72;}